export default {
	props: ['matter','hideBookInfo','finished'],
	data() {
		return {
			contentList: []
		}
	},
	computed: {
		themeFont () {
			return this.$store.state.themeFont
		}
	},
	watch: {
		matter(res){
			const data = JSON.parse(res.content)
			this.contentList = data.content.split('\n')
		}
	},
	methods: {
		previousHandle() {
			this.$emit('previous')
		},
		nextHandle(){
			this.$emit('next')
		},
		tapHandle() {
			this.$emit('tapHandle')
		}
	}
}
